import React, { Fragment, useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useParams } from "react-router-dom";

import { toast, ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { OutTable, ExcelRenderer } from "react-excel-renderer";

import Modal from "react-modal";
import styles from "./Modal.module.css";
import { onFetchingLocations } from "../../store/Locations/LocationsReducers";

const styleLinkBack = {
  textDecoration: "none",
  color: "#717993",
  fontSize: "22px",
};
const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
  },
};
const ViewLocs = () => {
  const { id } = useParams();

  const token = useSelector((state) => state.login.token);
  // const { selectedIdentifier } = useSelector((state) => state.globalIdentifier);
  // const { selectedEditProject } = useSelector((state) => state.projects);
  // const { selectedEditLocation } = useSelector((state) => state.locations);

  const [globalIdentifier, setGlobalIdenetifier] = useState(null);
  const [project, setProject] = useState(null);
  const [location, setLocation] = useState(null);
  const dispatch = useDispatch();

  const [addFileIsOpen, setAddFileIsOpen] = React.useState(false);
  const [flag, setFlag] = useState(false);

  const onUploadFile = (e) => {
    e.preventDefault();
    const formData = new FormData();
    formData.append("LocFile", file);

    console.log("65", file);
    fetch(
      `http://${localStorage.getItem("ipAddress")}:3001/api/LOCs/upload/${id}`,
      {
        method: "POST",
        headers: { Authorization: `Bearer ${token}` },
        body: formData,
      }
    )
      .then((res) => res.json())
      .then((resData) => {
        if (resData[0]) {
          toast("Error in upload");
          console.log(resData);
        }
        if (resData.message) {
          toast.success(resData.message);
          setFlag(true);
        }
        setFlag(false);
      });
  };

  useEffect(() => {
    dispatch(onFetchingLocations(id, token));

    fetch(
      `http://${localStorage.getItem(
        "ipAddress"
      )}:3001/api/locations/${id}?mode=local`,
      {
        headers: { Authorization: `Bearer ${token}` },
      }
    )
      .then((res) => res.json())
      .then((resData) => {
        console.log(resData);
        setGlobalIdenetifier(resData.globalIdentifier);
        setProject(resData.project);
        setLocation(resData.location);
      });
  }, [dispatch, flag]);

  const [state, setState] = useState();
  const [file, setFile] = useState({});
  const fileHandler = (event) => {
    let fileObj = event.target.files[0];

    console.log("44", fileObj);
    setFile(fileObj);
    //just pass the fileObj as parameter
    ExcelRenderer(fileObj, (err, resp) => {
      if (err) {
        console.log(err);
      } else {
        setState({
          cols: resp.cols,
          rows: resp.rows,
        });

        console.log("57", state);
      }
    });
  };

  return (
    <Fragment>
      <div className="container">
        <ToastContainer />
        {globalIdentifier?.name && project?.name && location?.name ? (
          <Fragment>
            <Link
              to={"/globalidenetifiers/projects/" + globalIdentifier.gid}
              style={styleLinkBack}
            >
              {globalIdentifier.name}
            </Link>
            <span className="mx-2" style={{ color: "#28345C" }}>
              <i className="fas fa-chevron-right"></i>
              <i className="fas fa-chevron-right"></i>
            </span>
            <Link to={"/locations/" + project.id} style={styleLinkBack}>
              {project.name}
            </Link>
            <span className="mx-2" style={{ color: "#28345C" }}>
              <i className="fas fa-chevron-right"></i>
              <i className="fas fa-chevron-right"></i>
            </span>
            <Link to={"/viewlocs/" + location.id} style={styleLinkBack}>
              {location.name}
            </Link>
          </Fragment>
        ) : null}

        {/* {selectedEditProject.name && selectedIdentifier.name ? (
          <Fragment>
            <Link
              to={"/globalidenetifiers/projects/" + selectedEditProject.gid}
              style={styleLinkBack}
            >
              {selectedIdentifier.name}
            </Link>
            <span className="mx-2" style={{ color: "#28345C" }}>
              <i className="fas fa-chevron-right"></i>
              <i className="fas fa-chevron-right"></i>
            </span>
            <Link
              to={"/locations/" + selectedEditProject.id}
              style={styleLinkBack}
            >
              {selectedEditProject.name}
            </Link>
            <span className="mx-2" style={{ color: "#28345C" }}>
              <i className="fas fa-chevron-right"></i>
              <i className="fas fa-chevron-right"></i>
            </span>
            <Link
              to={"/viewlocs/" + selectedEditLocation.id}
              style={styleLinkBack}
            >
              {selectedEditLocation.name}
            </Link>
          </Fragment>
        ) : null} */}

        {/* <Link to={"/gid"} style={styleLinkBack}>
            GID 1
        </Link>
        <span className="mx-2" style={{ color: "#28345C" }}>
          <i className="fas fa-chevron-right"></i>
          <i className="fas fa-chevron-right"></i>
        </span>
        <Link to={"/gid"} style={styleLinkBack}>
            Project 1
        </Link>
        <span className="mx-2" style={{ color: "#28345C" }}>
          <i className="fas fa-chevron-right"></i>
          <i className="fas fa-chevron-right"></i>
        </span>
        <Link to={"/gid"} style={styleLinkBack}>
            Location 1
        </Link> */}
        <div className="row my-5">
          <div className="col-12 col-md-8 col-lg-6 m-auto">
            <div className="d-flex justify-content-center">
              <Link
                to={"/CreateSingleLocInfoLocal/" + id}
                className="btn btn-primary p-3 w-50"
              >
                Create New Single LOC Info Local
              </Link>
            </div>
          </div>
        </div>

        <div className="row">
          <div className="col-12 col-md-8 col-lg-6 m-auto">
            <div className="d-flex justify-content-center">
              <Link
                to={"/CreateDualLocInfoLocal/" + id}
                className="btn btn-primary p-3 w-50"
                // style={{ backgroundColor: "#09B13B" }}
              >
                Create New Dual LOC Info Local
              </Link>
            </div>
          </div>
        </div>

        <div className="row mt-5">
          <div className="col-12 col-md-8 col-lg-6 m-auto">
            <div className="d-flex justify-content-center">
              <button
                type="button"
                className="btn btn-success p-3 w-50"
                data-bs-toggle="modal"
                data-bs-target="#ImportModal"
                onClick={() => setAddFileIsOpen(true)}
              >
                Import New LOC Via.Xisx
              </button>
            </div>
          </div>
        </div>
      </div>

      {/* Model */}
      <Modal
        isOpen={addFileIsOpen}
        style={customStyles}
        onRequestClose={() => setAddFileIsOpen(false)}
        contentLabel="Add Modal"
        ariaHideApp={false}
        className={styles.Modal}
        overlayClassName={styles.Overlay}
      >
        <div className="modal-body">
          <div className="row">
            <form
              onSubmit={(e) => {
                onUploadFile(e);
                setAddFileIsOpen(false);
              }}
            >
              <div className="col">
                <h3 className="text-center mt-5 text-primary">
                  IMPORT LOC INFO
                </h3>
                <div className="d-flex justify-content-center">
                  <label className="btn btn-outline-primary btn-sm my-3">
                    <input
                      type="file"
                      style={{ display: "none" }}
                      onChange={(e) => fileHandler(e)}
                    />
                    Choose File
                  </label>
                </div>
                <p className="text-center">Only .xlsx files can be uploaded</p>
                <div className="d-flex justify-content-center mb-5">
                  <button type="submit" className="btn btn-primary btn-sm w-25">
                    Upload
                  </button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </Modal>
      {/* End Model */}
    </Fragment>
  );
};
export default ViewLocs;
